import styled from 'styled-components';

export const Container = styled.div`
    padding: 0 15%;
    margin-right: auto;
    margin-left: auto;

    @media screen and (max-width: 1200px) {
        padding: 0 10%;
    }

    @media screen and (max-width: 745px) {
        padding: 0 5%;
    }
`